export const GDPR_EN_HTML: string = STYLE + `
<h1>Privacy Policy</h1>
<h2>Introduction</h2>
<p>Welcome to Thanker! Your privacy is important to us, and we are committed to protecting your personal data. This privacy policy explains how we collect, use, and store your information when you use our gratitude platform, Thanker.</p>
<h2>Data We Collect</h2>
<h3>Personal Data</h3>
<p>When you register on Thanker, we collect the following personal data:</p>
<ul>
<li><strong>Email Address</strong>: Used for account registration, login, and communication.</li>
<li><strong>Encrypted Password</strong>: Your password is encrypted before storage to ensure security.</li>
<li><strong>User Handle</strong>: A unique identifier chosen by you, used to identify your profile.</li>
</ul>
<h3>Gratitude Posts</h3>
<p><strong>Posts</strong>: Your gratitude posts, including those on your profile and those posted on others' profiles (with their permission), are collected. Each post's text is encrypted before it reaches our database to protect your privacy.</p>
<h2>How We Use Your Data</h2>
<h3>Service Provision</h3>
<ul>
<li><strong>Account Management</strong>: We use your email address and encrypted password to manage your account and authenticate your access to Thanker.</li>
<li><strong>Profile Identification</strong>: Your user handle allows other users to find and interact with your profile.</li>
<li><strong>Posts Display</strong>: Gratitude posts are displayed on your profile and the profiles of other users, as per your permissions.</li>
</ul>
<h3>Communication</h3>
<ul>
<li><strong>Notifications</strong>: We use your email address to send you notifications about important updates, changes to our terms and services, and other relevant information.</li>
<li><strong>Support</strong>: Your email address is used to respond to your support requests and inquiries.</li>
</ul>
<h2>Data Security</h2>
<h3>Encryption</h3>
<ul>
<li><strong>Password Encryption</strong>: Your password is encrypted using industry-standard encryption techniques before it is stored in our database.</li>
<li><strong>Post Encryption</strong>: The text of your gratitude posts is encrypted before being stored in our database to ensure the privacy and security of your content.</li>
</ul>
<h3>Access Control</h3>
<ul>
<li><strong>Restricted Access</strong>: Access to your personal data is restricted to authorized personnel who need it to perform their job functions.</li>
</ul>
<h2>Data Sharing</h2>
<h3>Third Parties</h3>
<p>We do not share your personal data with third parties, except where required by law or necessary to protect our legal rights.</p>
<h3>User Permissions</h3>
<ul>
<li><strong>Profile Posts</strong>: Other users can post on your profile only if you have given them permission to do so.</li>
</ul>
<h2>Your Rights</h2>
<h3>Access and Control</h3>
<ul>
<li><strong>Access</strong>: You have the right to access your personal data and request a copy of the information we hold about you.</li>
<li><strong>Correction</strong>: You can request corrections to your personal data if it is inaccurate or incomplete.</li>
<li><strong>Deletion</strong>: You have the right to request the deletion of your personal data, subject to certain conditions.</li>
<li><strong>Restriction</strong>: You can request the restriction of processing your personal data under certain circumstances.</li>
<li><strong>Portability</strong>: You have the right to request the transfer of your personal data to another service provider.</li>
</ul>
<h3>Consent Withdrawal</h3>
<ul>
<li><strong>Withdraw Consent</strong>: You can withdraw your consent to the collection and processing of your personal data at any time. This may affect your ability to use certain features of Thanker.</li>
</ul>
<h2>Contact Us</h2>
<p>If you have any questions, concerns, or requests regarding your personal data or this privacy policy, please contact us at:</p>
<ul>
<li><strong>Email</strong>: beatrue@thanker.com</li>
</ul>
<h2>Changes to This Policy</h2>
<p>We may update this privacy policy from time to time. We will notify you of any significant changes by email or through a notice on our platform.</p>
<p>By using Thanker, you agree to the terms outlined in this privacy policy. Thank you for trusting us with your personal data and for being a part of our gratitude community.</p>
`;

